<template>
  <div class="container">
    <el-row>
      <el-col :span="12">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: 'inform' }">消息列表</el-breadcrumb-item>
          <el-breadcrumb-item>来自社区</el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
      <el-col :span="12" class="badge">
        <el-breadcrumb>
          <el-link :underline="false" type="primary"><span style="font-weight: bolder"></span></el-link>
        </el-breadcrumb>
      </el-col>
    </el-row>

    <el-row>
      <el-divider></el-divider>
    </el-row>

    <el-row class="divider">
      <template>
          <div v-for="info in information_list">
             <el-row>
               <el-col :span="18">
             <span :class="info.read ===1 ? 'light_gray':''">
            来自&nbsp;&nbsp;{{info['inform_from'].hospital_name}}
            的&nbsp;&nbsp;{{info['patient'].patient_name}}&nbsp;&nbsp;向您发起催诊提醒，
            请您尽快诊断
            </span>
               </el-col>
               <el-col :span="4">
                 <el-row style=" min-width: 200px" :class="info.read ===1 ? 'light_gray':''">{{info['inform_time']}}</el-row>
               </el-col>

               <el-col :span="2">
                 <el-row style="text-align: center;min-width: 100px">
                   <el-col :span="12">
                     <el-link :underline="false" :disabled="info.read === 1"
                      @click="clickToDiagnose(info)"   icon="el-icon-view"  type="primary">查看</el-link>
                   </el-col>
                   <el-col :span="12">
                     <el-link :underline="false" @click="deleteInform(info)" icon="el-icon-delete" type="info">删除</el-link>
                     </el-col>
                 </el-row>
               </el-col>
             </el-row>
            <el-divider></el-divider>
          </div>
      </template>
    </el-row>

    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :total="totalNum">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import {
    get_information,
    removeInformation,
    clickDiagnoseButton,
      minusMessageMount} from '../../api/api';
  import url from "../../static/js/hostUrl";
  import "../../static/js/jquery.form";
  import get_message_status from '../../api/common';

  let host = url.url;
  export default {
    name:'inform',
    created:function(){
      this.currentPage = 1;
      this.get_info_list(1,10)
    },

    mounted:function(){

    },

    methods:{
      //获取通知信息
      get_info_list:function(p,page_size){
        let para = {};
        let inform_hospital = localStorage.getItem('superior_hospital');
        para['p'] = p;
        para['inform_hospital'] = inform_hospital;
        para['page_size'] = page_size;
        para['ordering'] = '-inform_time';
        get_information(para).then((res)=>{
          this.information_list = res['results'];
          this.totalNum = res['count'];
        });
      },

      //切换通知信息的页码
      handleCurrentChange:function (val) {
        this.currentPage = val;
        this.get_info_list(val,10);
      },

      // 删除通知
      deleteInform:function (row) {
        let para = {};
        para['id'] = row['id'];
        let read = row['read'];
        this.$confirm('确认删除该记录吗?', '提示', {
          type: 'warning'}).then(() => {
          removeInformation(para).then((async res=>{
            if(res===1){
              if(read === 0){
                /*当这条消息未被阅读时，删除它，则消息数量减一，已阅读，删除它，消息数量不减*/
                await this.messageAmountProcess(row);
                /*更新vuex中的消息状态来控制红点的显示隐藏*/
                await get_message_status();
              }
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            }else{
              this.$message({
                message: '删除失败',
                type: 'error'
              });
            }
            this.handleCurrentChange(this.currentPage,10)
          }))
        })

      },


      transferPinfo:function (editForm) {
        return new Promise(resolve => {
          let patient_info = {};
          patient_info['page_size'] = this.page_size;
          patient_info['currentPage'] = this.currentPage;
          patient_info['patient_name'] = editForm['patient'].patient_name;
          patient_info['patient_gender'] = editForm['patient'].patient_gender;
          patient_info['patient_age'] = editForm['patient'].patient_age;
          patient_info['patient_id'] = editForm['patient'].patient_id;
          patient_info['diagnose_record'] = editForm['patient'].diagnose_record;
          patient_info['apply_time'] = editForm['patient'].apply_time;
          patient_info['picture'] = editForm['patient'].picture;
          patient_info['diagnose'] = editForm['patient'].diagnose;
          patient_info['historyDisease'] = editForm['patient'].historyDisease;
          patient_info['hospital_id'] = editForm['patient'].hospital;
          patient_info['minusMessage'] = editForm['patient'].minusMessage;

          let superHosList = editForm.inform_hospital;
          let id_list = [];
          for(let i=0;i<superHosList.length;i++){
            id_list.push(superHosList[i]['id'])
          }
          patient_info['id_list'] = id_list;
          localStorage.setItem('patient_info', JSON.stringify(patient_info));
          resolve('done')
        });

      },

      getDiagnoseStatus:function(para){
        return new Promise(resolve => {
          clickDiagnoseButton(para).then((res) => {
            if(res === 0 ){
              this.$alert('该名患者正在被诊断，您暂时无法诊断', '温馨提示', {
                confirmButtonText: '确定',
                callback: action => {
                  console.log(action)
                }
              });

            }else if(res === 2){
              this.$alert('该名患者已经被诊断', '温馨提示', {
                confirmButtonText: '确定',
                callback: action => {
                  console.log(action)
                }
              });
            }
            resolve(res)
          });
        })

      },

      messageAmountProcess:function(row){
        let para2 = {};
        para2['patient_id'] = row['patient']['patient_id'];
        let superHosList = row.inform_hospital;
        let id_list = [];
        for(let i=0;i<superHosList.length;i++){
          id_list.push(superHosList[i]['id'])
        }
        para2['id_list'] = id_list;
        return new Promise(resolve => {
          minusMessageMount(para2).then((res) => {
            if(res === 1 ){
              console.log('消息减去成功')
            }
            else{
              console.log('消息减去失败')
            }
            resolve(res)
          });
        })
      },

      async clickToDiagnose(row){
        let editInfo = {};
        editInfo['patient_id'] = row[['patient']].patient_id;
        editInfo['doctor2'] = localStorage.getItem('user_id');
        this.$store.commit('changePageFrom','inform');
        let para1 = Object.assign({}, editInfo);
        let para2 = {};
        // para2['message_type'] = 'community';
        // para2['message_id'] = row.id;
        // let superHosList = row.inform_hospital;
        // let id_list = [];
        // for(let i=0;i<superHosList.length;i++){
        //   id_list.push(superHosList[i]['id'])
        // }
        let back1 = await this.getDiagnoseStatus(para1);
        // para2['id_list'] = id_list;
        // row['patient']['minusMessage'] = para2;
        console.log('1获取诊断状态',back1);
        if(back1 === 1){
          //let back2 = await this.messageAmountProcess(para2);
          let back3 = await this.transferPinfo(row);
          console.log('3获取跳转状态',back3);
          await this.$router.push('report');
        }
      },


    },


    data() {

      return {
        information_list :'',
        totalNum: 0,
        currentPage:'',
        isActive1:false,
        isActive2:true,
      }
    }
  }

</script>

<style scoped>
  .container{
    text-align: left;
    margin-top: 20px;
    min-width: 1100px;
  }

  .light_gray{
    color: lightgrey;
  }
  /*.timezone,.dataZone{*/
    /*margin-top: 20px;*/
  /*}*/
  /*.el-date,.el-icon-date,.icon-shuaxin1{*/
    /*color: #0F9273;*/
  /*}*/

  /*.operationNew{*/
    /*margin-top: 20px;*/
    /*text-align: center;*/
    /*line-height: 41px;*/
  /*}*/
  a{
    text-decoration: none;
    outline: none;
    color: #0F9273;
  }

  /*.el-input__inner{*/
    /*border: #0F9273 1px solid;*/
    /*color: #0F9273;*/
  /*}*/

  /*.active{*/
    /*border: 1px solid #0F9273;*/
    /*border-radius: 4%;*/
  /*}*/


  .pagination{
    margin-top: 20px;
    text-align: right;
  }

  .el-date-editor.el-input, .el-date-editor.el-input__inner{
    width: 140px;
  }

  .timepicker >>> i{
    color: #0F9273;
    font-size: large;
  }
  .timepicker >>> input {
    font-size: 15px;
  }

  .timepicker >>>input::-webkit-input-placeholder{
    color: #0F9273;
  }

  .timepicker >>>input::-moz-placeholder{
    color: #0F9273;
  }

  /*.el-loading-text{*/
  /*  font-size: x-large;*/
  /*}*/

  @media screen and (min-width:600px) { /*大于600px小于1650px*/
    .container {
      /*background-color: blue;*/
    }

  }

  @media screen and (min-width:1650px) { /*大于1650px小于1900px*/
    .container {
      /*background-color: red;*/
    }
  }

  @media screen and (min-width:1900px) {  /*大于1900px*/
    .container {
      /*background-color: yellow;*/
    }
  }

  .el-dropdown {
    vertical-align: top;
  }
  .el-dropdown + .el-dropdown {
    margin-left: 15px;
  }
  .el-icon-arrow-down {
    font-size: 12px;
  }

  .badge{
    display: inline-block;
    text-align: right;
    padding-right: 20px;
    font-weight: bold;
  }
  .block{
    float: right;
  }

</style>



